.social {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
}

.social__link {
  display: block;

  &:hover {
    & path {
      fill: $basic-color;
    }
  }

  &--fb:hover {
    & path {
      fill: none;
      stroke: $basic-color;
    }
  }

  &:active {
    opacity: 30%;
  }
}
