.textarea__input {
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  padding: 8px 12px 13px 15px;

  font-family: $special-font-stack;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  color: $basic-text-color;
  border: 1px solid #e7e7e7;

  resize: none;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    height: 174px;
  }
}
